#home {
    z-index: 10;
}

#title {
    display: block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    top: calc(50% - 250px);
    font-family: inherit;
    font-size: 24px;
    font-weight: bold;
}

#version {
    display: block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    top: calc(50% - 270px);
    font-family: inherit;
    font-size: 18px;
    font-weight: bold;
}

#home-logo {
    height: 120px;
    width: 120px;
    object-fit: contain;
    display: block;
    left: calc(50% - 60px);
    top: calc(50% - 290px);
    position: relative;
}

#home-btns-div {
    height: 80px;
    width: 120px;
    position: relative;
    top: calc(50% - 240px);
    margin-left: auto;
    margin-right: auto;
}

#home-btns-div > button {
    display: block;
    height: 40px;
    width: 100%;
    border-radius: 5px;
    font-family: inherit;
    font-size: 18px;
    border-width: 5px;
    border: 0px;
    background-color: rgb(0, 0, 0, 0);
    position: relative;
    font-weight: bold;
    top: 0px;
}
